import { useState } from 'react';
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Snackbar, Alert, Box } from '@mui/material';
import { useProxyWatch, useTranslation } from '@fanfanlo';
import { tmsUserDeleteAccount, user, trackingPrint } from '@app/model';

export const DeleteAccountButton = () => {
    const { t } = useTranslation("emergency-call/pages/settings/content");


        const [token] = useProxyWatch(user, 'data.storeData.user_token', user.data.storeData.user_token);

    const [openDialog, setOpenDialog] = useState(false);
    const [snackbar, setSnackbar] = useState<{ open: boolean; message: string; severity: 'success' | 'error' }>({
        open: false,
        message: '',
        severity: 'success',
    });

    const handleOpenDialog = () => {
        // 删除账户对话框打开埋点
        trackingPrint({params:{
            type:"模态框操作",
            title:"删除账户",
            subtitle:"确认对话框-打开",
            extra:{
                timestamp: Date.now(),
                hasToken: !!token
            }
        }});
        setOpenDialog(true);
    };

    const handleCloseDialog = () => {
        // 删除账户对话框关闭埋点
        trackingPrint({params:{
            type:"模态框操作", 
            title:"删除账户",
            subtitle:"确认对话框-取消关闭",
            extra:{
                timestamp: Date.now()
            }
        }});
        setOpenDialog(false);
    };


// ... (inside the component)

    const handleConfirmDelete = async () => {
        // 确认删除账户埋点
        trackingPrint({params:{
            type:"用户操作",
            title:"删除账户",
            subtitle:"确认删除-点击",
            extra:{
                timestamp: Date.now(),
                userId: token
            }
        }});
        
        handleCloseDialog();
        try {
            const response = await tmsUserDeleteAccount({ params: {} });
            if(response.shellIsOk && response.dataIsOk){
                // 删除成功埋点
                trackingPrint({params:{
                    type:"用户操作",
                    title:"删除账户",
                    subtitle:"删除成功",
                    extra:{
                        timestamp: Date.now(),
                        redirectDelay: 2000
                    }
                }});
                
                user.signout()
                setSnackbar({ open: true, message: t('content.deleteAccount.success'), severity: 'success' });
                // After a short delay, redirect
                setTimeout(() => {
                    window.location.href = '/';
                }, 2000);
            } else {
                const errorMessage = response.data?.msg || response.err?.message || 'Unknown error';
                
                // 删除失败埋点
                trackingPrint({params:{
                    type:"系统异常",
                    title:"删除账户",
                    subtitle:"删除失败",
                    extra:{
                        timestamp: Date.now(),
                        error: errorMessage,
                        shellIsOk: response.shellIsOk,
                        dataIsOk: response.dataIsOk
                    }
                }});
                
                setSnackbar({ open: true, message: t('content.deleteAccount.failure', { error: errorMessage }), severity: 'error' });
            }
        } catch (error) {
            const errorMessage = error instanceof Error ? error.message : 'An unknown error occurred';
            
            // 删除异常埋点
            trackingPrint({params:{
                type:"系统异常",
                title:"删除账户",
                subtitle:"删除异常",
                extra:{
                    timestamp: Date.now(),
                    error: errorMessage,
                    stack: error instanceof Error ? error.stack : undefined
                }
            }});
            
            setSnackbar({ open: true, message: t('content.deleteAccount.failure', { error: errorMessage }), severity: 'error' });
        }
    };

    const handleCloseSnackbar = () => {
        setSnackbar({ ...snackbar, open: false });
    };

        if(!token){
            return <></>
        }
    return (
        <Box sx={{ mt: 2 }}>
            <Button variant="contained" color="error" onClick={handleOpenDialog}>
                {t('content.deleteAccount.button')}
            </Button>

            <Dialog
                open={openDialog}
                onClose={handleCloseDialog}
            >
                <DialogTitle>{t('content.deleteAccount.button')}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        {t('content.deleteAccount.confirm')}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCloseDialog}>取消</Button>
                    <Button onClick={handleConfirmDelete} color="error" autoFocus>
                        确认注销
                    </Button>
                </DialogActions>
            </Dialog>

            <Snackbar open={snackbar.open} autoHideDuration={6000} onClose={handleCloseSnackbar}>
                <Alert onClose={handleCloseSnackbar} severity={snackbar.severity} sx={{ width: '100%' }}>
                    {snackbar.message}
                </Alert>
            </Snackbar>
        </Box>
    );
};
