import { IRequestParams, IResultContent, request } from "@fanfanlo";
import { IRequestOptions } from "../IRequestOptions";
import { tms } from "../tms";

// This request does not require any specific parameters beyond the default ones.
export interface IDeleteAccountParams extends IRequestParams {}

// The expected result from the backend.
export interface IDeleteAccountResult extends IResultContent {
    isOk: boolean;
    msg: string;
}

/**
 * Calls the endpoint to delete a user account.
 * @param opts Request options, can be empty as no specific params are needed.
 * @returns The result from the server.
 */
export async function tmsUserDeleteAccount(opts: IRequestOptions<IDeleteAccountParams>) {
    const res = await request<IDeleteAccountResult>(tms.TUser, tms.MUserDeleteAccount, opts.params, opts.gateway, opts.handler);
    if (!res.dataIsOk) {
        console.error('tmsUserDeleteAccount error:', res.err, res);
    }
    return res;
}

