import { Box, Button } from "@mui/material";
import { ReportButton } from "./ReportButton";
import { useRouter } from "next/router";


export function MyReports(){
  const router = useRouter()
    return(
        
      <Box sx={{mt:2, mb:2}}>
      <ReportButton />
      <Button onClick={()=>router.push('/my-reports')}>
        我的举报
      </Button>
      </Box>
    )
}