import { Button } from "@mui/material";
import { useFontSize } from "@src/contexts/FontSizeContext";

export const FontSizeButton = () => {
  const { openDialog } = useFontSize();
  
  const handleOpenFontSizeDialog = () => {
    openDialog(false); // false 表示不是从首页打开的
  };
  
  return (
    <Button 
      fullWidth 
      variant="outlined" 
      onClick={handleOpenFontSizeDialog}
      startIcon={
        <span style={{ fontSize: '1.2em' }}>🔍</span>
      }
      sx={{
        mt:2,
        mb: 2,
        py: 1.5,
        justifyContent: 'flex-start',
        textTransform: 'none',
        fontSize: '1rem',
        '& .MuiButton-startIcon': {
          marginRight: 1.5,
        }
      }}
    >
      调整字体大小
    </Button>
  );
};
