import { trackingPrint } from "@app/model";
import { Box, Button } from "@mui/material";
import { PrivacyDialogDisplay } from "@src/components/privacies/PrivacyDialogDisplay";
import { usePrivacyDialog } from "@src/components/privacies/usePrivacyDialog";
import { MyReports } from "@src/components/report/MyReports";
import { FontSizeButton } from "@src/components/settings/FontSizeButton";
import { SignUpInButton } from "../../sign-up-in/components/sign-up-in-button/SignUpIn";
import { AppBuildTime } from "./buildtime/AppBuildTime";
import { DeleteAccountButton } from "./DeleteAccountButton";

// import {LanguageSelect} from '../../../language-selector/LanguageSelect'

function Privacy() {
  const { open, openDialog, closeDialog } = usePrivacyDialog();

  const handleOpenDialog = () => {
    // 隐私协议按钮点击埋点
    trackingPrint({params:{
      type:"用户操作",
      title:"隐私协议",
      subtitle:"协议按钮-点击",
      extra:{
        timestamp: Date.now(),
        page: "settings"
      }
    }});
    openDialog();
  };

  const handleCloseDialog = () => {
    // 隐私协议对话框关闭埋点
    trackingPrint({params:{
      type:"模态框操作",
      title:"隐私协议",
      subtitle:"协议对话框-关闭",
      extra:{
        timestamp: Date.now()
      }
    }});
    closeDialog();
  };

  return (
    <Box sx={{ mt: 2 }}>
      <Button onClick={handleOpenDialog}>隐私协议</Button>
      <PrivacyDialogDisplay
        open={open}
        onClose={handleCloseDialog}
        type="main"
        area="zh-cn"
      />
    </Box>
  );
}

export const SettingsContent = () => {
  return (
    <Box sx={{ p: 2 }}>
      {/* <LanguageSelect /> */}
      
      <SignUpInButton back="/settings" />
      <DeleteAccountButton />
      <FontSizeButton />
      <AppBuildTime />
      
      {/* 语音呼救设置 */}
      {/* <VoiceEmergencySettings /> */}
      <MyReports/>
      <Privacy />
      
      {/* 保留自动弹出逻辑 */}
      {/* <PrivacyDialogAuto type="main" area="zh-cn" /> */}
      {/* <AppVersion /> */}
    </Box>
  );
};