import { useTranslation } from "@fanfanlo";
import { useRouter } from "next/router";
import { useProxyWatch } from "@fanfanlo";
import { user } from "@app/model";
import toast from "react-hot-toast";
import { Box, Button } from "@mui/material";
import Link from "next/link";
import { ButtonConfirm } from "@fanfanlo";
import { useEffect, useState } from "react";

interface SignUpInButtonProps{
    back?:string;
}

export function SignUpInButton({back}:SignUpInButtonProps){
    // apps\homepage\src\components\page\sign-up-in\components\sign-up-in-button\SignUpIn.i18n.json
    const { t } = useTranslation('homepage/components/page/sign-up-in/components/sign-up-in-button/content');
    const router = useRouter();
    const [token] = useProxyWatch(user, 'data.storeData.user_token', user.data.storeData.user_token);
    const [userInitialized] = useProxyWatch(user, 'data.userInitialized', user.data.userInitialized);
    const [userName, setUserName] = useState(user.userName || '');
    useEffect(()=>{
        setUserName(user.userName || '');
    }, [token, user.userName])
    function onSignoutClick(){
        user.signout();
        toast(t('SignUpIn.signoutOk'), { duration: 5000 })
    }
    if(!userInitialized){
        return (
            <>
            {t('SignUpIn.loadingData')}
            </>
        )
    }
    if(token){
        return(
            <Box>
                <Box>
                    <span>
                        {userName}
                    </span>
                </Box>
                <Box>
                <span>
                    <ButtonConfirm title={t('SignUpIn.confirmTitle')} content={t('SignUpIn.confirmContent')} onConfirm={onSignoutClick}><Button>{t('SignUpIn.signOut')}</Button></ButtonConfirm>
                </span>
                </Box>
            </Box>
        )
    }
    return (
        <>
            <span>
                <Link href={`/sign-up-in?back=${back}`}>{t('SignUpIn.signUpIn')}</Link>
            </span>
        </>
    )
}
